import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { AppSettings } from '../../app.setting';

import { AuthenticationService } from '../authentication.service';

@Injectable()
export class TemplateService {
  constructor(
    private http: Http,
    private authenticationService: AuthenticationService) {
  }

  getMyTemplates(params): Observable<any> {
    // add authorization header with jwt token
    const headers = new Headers({ 'Authorization': 'Bearer ' + this.authenticationService.token });
    const options = new RequestOptions({ headers: headers });

    return this.http.get(`${AppSettings.API_ENDPOINT}/my_templates`, options)
      .map((response: Response) => response.json());
  }
  getPredefinedTemplates(params): Observable<any> {
    // add authorization header with jwt token
    const headers = new Headers({ 'Authorization': 'Bearer ' + this.authenticationService.token });
    const options = new RequestOptions({ headers: headers });

    return this.http.get(`${AppSettings.API_ENDPOINT}/predefined_templates`, options)
      .map((response: Response) => response.json());
  }
}



